.header{
  height: 8em;
  padding: 0px 3em 0px 3em;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background-color: #212121;
}

.header-text{
  color: white;
}

.header-heading{
  font-size: 3em;
}

.header-paragraph{
  font-size: 1.2em;
  letter-spacing: 1px;
}

.header-buttons{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.header-buttons button:nth-child(-n+2){
  margin-right: 1em;
  padding: 8px 18px 8px 18px;
  cursor: pointer;
  border-radius: 5px;
  outline: none;
  border: none;
  box-shadow: 2px 2px 2px #212121;
  font-size: 1.5em;
  letter-spacing: 1px;;
  transition: all 0.4s;
}

.header-buttons button:nth-child(2){
  background-color: #FF5252;
}
.header-buttons button:hover{
  box-shadow: 5px 5px 5px #212121;
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.toggler{
  display: none;
}

/* MEDIA QUERY FOR NAV TOGGLER : md-screen */
@media screen and (max-width: 690px){
  .toggler{
    display:block;
    font-size: 2.5em;
    color:aliceblue;
    cursor: pointer;
  }
  
  .header-buttons.responsive{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 60vw;
    height:60vh;
    position: absolute;
    right: 0px;
    top: 8em;
    background-color: #212121;
    opacity: 0.8;
    -webkit-animation: toggleAnimation 1s;
            animation: toggleAnimation 1s;
  }
  
  .header-buttons{
    display: none;
  }
  .header-buttons.responsive button{
    margin-bottom: 5em;
  }
}

/* MEDIA QUERY FOR NAV TOGGLER : sm-screen */
@media screen and (max-width: 470px){
  .header-buttons.responsive{
    width: 95vw;
  }

  .header-heading{
    font-size: 2em;
  }
  
  .header-paragraph{
    font-size: 1em;
  }
}

/* TOGGLING NAV ANIMATION */
@-webkit-keyframes toggleAnimation{
  from { right: -20em}
  to { right: 0px}
}
@keyframes toggleAnimation{
  from { right: -20em}
  to { right: 0px}
}
