.custom-loader {
  width: 100px;
  height: 100px;
  --c: radial-gradient(farthest-side,#ff5252 92%,#0000);
  background: 
    var(--c) 50%  0, 
    var(--c) 50%  100%, 
    var(--c) 100% 50%, 
    var(--c) 0    50%;
  background-size: 20px 20px;
  background-repeat: no-repeat;
  -webkit-animation: s8 1s infinite;
          animation: s8 1s infinite;
  position: relative;
}
.custom-loader::before {    
  content:"";
  position: absolute;
  inset:0;
  margin: 6px;
  background:repeating-conic-gradient(#0000 0 35deg,#ff5252 0 90deg);
  -webkit-mask:radial-gradient(farthest-side,#0000 calc(100% - 6px),#000 0);
  border-radius: 50%;
}

@-webkit-keyframes s8 { 
  100%{-webkit-transform: rotate(.5turn);transform: rotate(.5turn)}
}

@keyframes s8 { 
  100%{-webkit-transform: rotate(.5turn);transform: rotate(.5turn)}
}
